<template>
  <div>
    <span class="name">按省份选择：</span>
    <m-select
      :list="provinceList"
      :value="province"
      title="省份"
      :showWrapperActive="provinceActive"
      @change_active="changeProvinceActive"
      @change="changeProvince"
      className="province"
    />
    <m-select
      :list="cityList"
      :value="city"
      title="城市"
      :showWrapperActive="cityActive"
      @change_active="changeCityActive"
      @change="changeCity"
      :disabled="cirtDisabled"
      className="city"
    />
    <span>直接搜索：</span>
    <el-select
      v-model="searchWord"
      filterable
      remote
      reserve-keyword
      placeholder="请输入关键词"
      :remote-method="remoteMethod"
    >
      <el-option v-for="(item,index) in searchList" :key="index" :label="item" :value="item"></el-option>
    </el-select>
  </div>
</template>

<script>
import MSelect from './select.vue'
import api from '@/api/index.js'

export default {
  data () {
    return {
      provinceList: [],
      province: '省份',
      city: '城市',
      cityList: [],
      provinceActive: false,
      cityActive: false,
      searchList: [
        '昆明',
        '大理',
        '广州',
        '深圳',
        '珠海',
        '西双版纳',
        '深圳',
        '珠海',
        '西双版纳',
        '深圳',
        '珠海',
        '西双版纳'
      ],
      searchWord: '',
      cirtDisabled: true
    }
  },
  created () {
    api.getProvince().then((res) => {
      // console.log(res.data.data);
      this.provinceList = res.data.data
      /* for(const p of res.data.data){
        this.provinceList.push(p.provinceName);
      } */
      // this.provinceList = res.data.data.map(item=>item.provinceName);
    })
  },
  components: {
    MSelect
  },
  methods: {
    changeProvinceActive (flag) {
      this.provinceActive = flag
      if (flag) {
        this.cityActive = false
      }
    },
    changeCityActive (flag) {
      this.cityActive = flag
      if (flag) {
        this.provinceActive = false
      }
    },
    changeCity (item) {
      this.city = item
      this.$store.dispatch('setPosition', item)
      this.$router.push({ name: 'index' })
    },
    changeProvince (item) {
      this.cirtDisabled = false
      this.province = item.provinceName
      this.cityList = item.cityInfoList.map(i => {
        return i.name
      })
    },
    remoteMethod () {
      // 请求后端接口
    }
  }
}
</script>

<style lang='scss'>
@import "@/assets/css/changecity/iselect.scss";
</style>
