<template>
  <div class="m-geo">
    <div class="position">
      <i class="el-icon-location" />
      {{ $store.state.position }}
      <router-link class="changeCity" :to="{name: 'changeCity'}">切换城市</router-link>
      <!-- <router-link class="" to="/"> -->
      [
      <a href="#" v-for="(item) in nearCity" :key="item.id">&nbsp;{{ item.name }}&nbsp;</a>
      ]
      <!-- </router-link> -->
    </div>
    <div class="m-user" v-if="!$store.state.userName">
      <router-link class="login" :to="{name: 'login'}">立即登录</router-link>
      <router-link class="register" :to="{name: 'register'}">注册</router-link>
    </div>
    <div class="m-user" v-else>
      <div>{{$store.state.userName}}</div>

    </div>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      nearCity: []
    }
  },
  watch: {
    '$store.state.position': function () {
      /* api.getCurPosition().then((res) => {
        this.$store.dispatch("setPosition", res.data.data.name);
        this.nearCity = res.data.data.nearCity;
      }); */
      this.nearCity = this.$store.state.position// 我的position不是城市数据的对象，只有城市名
    }
  },
  created () {
    api.getCurPosition().then((res) => {
      // console.log(res.data.data);
      this.$store.dispatch('setPosition', res.data.data.name)
      this.nearCity = res.data.data.nearCity
    })
  }
}
</script>

